.app-container {
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  color: white;
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
}

.title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.section {
  margin: 2rem auto;
  padding: 1.5rem;
  background-color: rgba(220, 45, 45, 0.1);
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  width: 80%;
  max-width: 500px;
}

.display {
  font-size: 2rem;
  margin: 1rem 0;
}

input[type="time"] {
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 10px;
  border: none;
}

button {
  font-size: 1rem;
  margin: 0.5rem;
  padding: 0.6rem 1.2rem;
  background-color: #ffffff22;
  color: white;
  border: 1px solid white;
  border-radius: 10px;
  cursor: pointer;
}

button:hover {
  background-color: #ffffff44;
}
